name: "mPublishPage",
data(){
return{
api: this.GLOBAL.baseURL,
height:"",
screnWidth:'',
opacity:0,
top:-50,
imageList:[
{img: '../../../static/img/static/2.jpg'},
{img: '../../../static/img/static/1.jpg'},
{img: '../../../static/img/static/3.jpg'},
],
disList:[],
houseList:[
{img: '../../../static/img/static/2.jpg'},
{img: '../../../static/img/static/1.jpg'},
{img: '../../../static/img/static/3.jpg'},
],
score:4,
bottom: -60,
jionDress:[],
activeData:'',
province:'',
imgList:[],
isOwer:"",
return_policy:'',
policy:[],
long_day:'',
houseImage:[]
}
},
components:{
LoadingImg,
Swiper,
SwiperItem,
Rater,
Cell,
Map
},
watch:{
'$route':'getParams',
province:function(){
this.$nextTick(()=>{
Bus.$emit('jionDress',[this.activeData.set_address_lng,this.activeData.set_address_lat])
Bus.$emit('tyas',2)
})
}
},
methods:{
